'use strict';

var path = require('path');

var execa = require('execa');

var arch = require('arch'); // Binaries from: https://github.com/sindresorhus/win-clipboard


var winBinPath = arch() === 'x64' ? path.join(__dirname, '../fallbacks/windows/clipboard_x86_64.exe') : path.join(__dirname, '../fallbacks/windows/clipboard_i686.exe');
module.exports = {
  copy: function copy(opts) {
    return execa(winBinPath, ['--copy'], opts);
  },
  paste: function paste(opts) {
    return execa.stdout(winBinPath, ['--paste'], opts);
  },
  copySync: function copySync(opts) {
    return execa.sync(winBinPath, ['--copy'], opts);
  },
  pasteSync: function pasteSync(opts) {
    return execa.sync(winBinPath, ['--paste'], opts);
  }
};