'use strict';

var path = require('path');

var execa = require('execa');

var handler = function handler(err) {
  if (err.code === 'ENOENT') {
    throw new Error('Couldn\'t find the required `xsel` binary. On Debian/Ubuntu you can install it with: sudo apt install xsel');
  }

  throw err;
};

var xsel = path.join(__dirname, '../fallbacks/linux/xsel');
module.exports = {
  copy: function copy(opts) {
    return execa(xsel, ['--clipboard', '--input'], opts).catch(function () {
      return execa('xsel', ['--clipboard', '--input'], opts);
    }).catch(handler);
  },
  paste: function paste(opts) {
    return execa.stdout(xsel, ['--clipboard', '--output'], opts).catch(function () {
      return execa.stdout('xsel', ['--clipboard', '--output'], opts);
    }).catch(handler);
  },
  copySync: function copySync(opts) {
    try {
      return execa.sync(xsel, ['--clipboard', '--input'], opts);
    } catch (err) {
      try {
        return execa.sync('xsel', ['--clipboard', '--input'], opts);
      } catch (err) {
        handler(err);
      }
    }
  },
  pasteSync: function pasteSync(opts) {
    try {
      return execa.sync(xsel, ['--clipboard', '--output'], opts);
    } catch (err) {
      try {
        return execa.sync('xsel', ['--clipboard', '--output'], opts);
      } catch (err) {
        handler(err);
      }
    }
  }
};