'use strict';

var execa = require('execa');

var handler = function handler(err) {
  if (err.code === 'ENOENT') {
    throw new Error('Couldn\'t find the termux-api scripts. You can install them with: apt install termux-api');
  }

  throw err;
};

module.exports = {
  copy: function copy(opts) {
    return execa('termux-clipboard-set', opts).catch(handler);
  },
  paste: function paste(opts) {
    return execa.stdout('termux-clipboard-get', opts).catch(handler);
  },
  copySync: function copySync(opts) {
    try {
      return execa.sync('termux-clipboard-set', opts);
    } catch (err) {
      handler(err);
    }
  },
  pasteSync: function pasteSync(opts) {
    try {
      return execa.sync('termux-clipboard-get', opts);
    } catch (err) {
      handler(err);
    }
  }
};