'use strict';

var execa = require('execa');

var env = Object.assign({}, process.env, {
  LC_CTYPE: 'UTF-8'
});
module.exports = {
  copy: function copy(opts) {
    return execa('pbcopy', Object.assign({}, opts, {
      env: env
    }));
  },
  paste: function paste(opts) {
    return execa.stdout('pbpaste', Object.assign({}, opts, {
      env: env
    }));
  },
  copySync: function copySync(opts) {
    return execa.sync('pbcopy', Object.assign({}, opts, {
      env: env
    }));
  },
  pasteSync: function pasteSync(opts) {
    return execa.sync('pbpaste', Object.assign({}, opts, {
      env: env
    }));
  }
};