'use strict';

var termux = require('./lib/termux.js');

var linux = require('./lib/linux.js');

var macos = require('./lib/macos.js');

var windows = require('./lib/windows.js');

function platform() {
  switch (process.platform) {
    case 'darwin':
      return macos;

    case 'win32':
      return windows;

    case 'android':
      if (process.env.PREFIX !== '/data/data/com.termux/files/usr') {
        throw new Error('You need to install Termux for this module to work on Android: https://termux.com');
      }

      return termux;

    default:
      return linux;
  }
}

exports.write = function (input) {
  if (typeof input !== 'string') {
    return Promise.reject(new TypeError("Expected a string, got ".concat(typeof input)));
  }

  return platform().copy({
    input: input
  }).then(function () {});
};

exports.read = function () {
  return platform().paste({
    stripEof: false
  });
};

exports.writeSync = function (input) {
  if (typeof input !== 'string') {
    throw new TypeError("Expected a string, got ".concat(typeof input));
  }

  platform().copySync({
    input: input
  });
};

exports.readSync = function () {
  return platform().pasteSync({
    stripEof: false
  }).stdout;
};